









import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import Labels from '@/views/Labels.vue'

@Component({
  components:{Labels}
})
export default class Layout extends Vue{
  @Prop() classPrefix !: string
  @Prop() value !: string[]
