
























import Vue from "vue";
import { Component, Emit, Prop } from "vue-property-decorator";

@Component
export default class Numbers extends Vue {
  // money = "0";
  @Prop()
  readonly value!: string;
  money = "0";
  mounted(): void {
    this.money = this.value;
  }
  inputContent(event: MouseEvent) {
    const button = event.target as HTMLButtonElement;
    const input = button.textContent as string;
    if (this.money.length === 8) {
      return;
    }
    if (this.money === "0") {
      if (input === "0") {
        return;
      }
      if ("123456789".indexOf(input) >= 0) {
        this.money = input;
      } else {
        this.money += input;
      }
      return;
    }
    if (this.money.indexOf(".") >= 0) {
      if (input === ".") {
        return;
      }
    }
    this.money += input;
    this.$emit("update:value", this.money);
  }
  remove(): void {
    if (this.money.length === 1) {
      this.money = "0";
    } else {
      this.money = this.money.slice(0, -1);
    }
    this.$emit("update:value", this.money);
  }
  @Emit("done")
  ok(){
    this.money = "0";
  }
}
